*, *::before, *::after {
    box-sizing: border-box; }

@import "normalizes.scss";
@import "resets.scss";



@mixin greyBG($width, $height) {
    width: $width;
    height: $height;
    background-color: rgba(217, 217, 217, 0.61);
    border-radius: 18px;
    display: block;
    margin: 0 auto;
    padding-top: 40px; }

@mixin darkBtn($width, $height) {
    width: $width;
    height: $height;
    background-color: #000000;
    border-radius: 12px;
    color: #C6C6C6;
    display: block;
    margin: 0 auto;
    margin-bottom: 28px; }

@mixin services {
    display: flex;
    flex-direction: column;
    font-size: clamp(7px, 3vw, 24px);
    line-height: clamp(10px, 7vw, 44px);
    letter-spacing: 10%;
    color: #000;
    opacity: 1; }

@mixin centered {
    margin: 0 auto;
    display: block; }


body {
    overflow-x: hidden; }

.main-section {
    @include centered;
    width: 100%;
    min-height: 100vh;
    background: url('../img/main-background.jpg') no-repeat center center / cover; }

.main-section__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%; }

.container {
    @include centered;
    width: 90%;
    max-width: 1200px;
    padding: 0 16px;
    height: auto;
    background: url('../img/background.jpg') no-repeat center center / cover;
    margin-bottom: 18px; }

.container > img {
    @include centered;
    width: 80%;
    aspect-ratio: 3 / 4;
    object-fit: cover;
    border-radius: 6px;
    padding-bottom: 16px;
    margin-top: 20px; }

.title {
    background: rgba(217, 217, 217, 0.78);
    display: block;
    border-radius: 14px;
    width: clamp(195px, 67vw, 935px);
    height: auto;
    margin: 0 auto;
    margin-bottom: 5vw; }

.title {
    h1 {
     font-size: clamp(20px, 3vw, 60px);
     line-height: clamp(1.2em, 3vw, 2em); } }

.title {
    h3 {
     font-size: clamp(15px, 2vw, 45px);
     line-height: clamp(1.2em, 2vw, 2em);
     margin-bottom: 1vw; } }

.roboto-mono-light {
    font-family: "Roboto Mono", monospace;
    font-optical-sizing: auto;
    font-weight: 300;
    font-style: normal; }

.manrope-light {
    font-family: "Manrope", sans-serif;
    font-optical-sizing: auto;
    font-weight: 300;
    font-style: normal; }

.manrope-semiBold {
    font-family: "Manrope", sans-serif;
    font-optical-sizing: auto;
    font-weight: 600;
    font-style: normal; }

.title__main-title_padding {
    padding: 7px 19px; }

.title__description_padding {
    padding: 17px 19px; }

.main-button-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 17px;
    margin-bottom: 15px; }

.main-button-section__btn {
    background: #000000;
    border: none;
    width: clamp(250px, 80vw, 940px);
    height: auto;
    padding: 2vh;
    border-radius: 12px;
    color: #C6C6C6;
    opacity: 0.7;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: clamp(25px, 3vw, 50px); }


.main-button-section {
    a {
        &:link,
        &:visited,
        &:hover,
        &:active,
        &:focus {
            color: #C6C6C6;
            text-decoration: none; } } }

.manrope-regular {
    font-family: "Manrope", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    line-height: 1.4;
    letter-spacing: 10%; }

.btn--opacity {
    opacity: 1; }

.main > img {
    @include centered;
    aspect-ratio: 1 / 1;
    object-fit: cover;
    border-radius: 25px;
    margin-bottom: 32px;
    width: clamp(200px, 50vw, 715px); }


.text-block {
    @include greyBG(clamp(250px, 80vw, 1218px), auto);
    padding: 2em;
    margin: 0 auto 40px auto; }

.text-block__title {
    font-size: clamp(20px, 3vw, 60px);
    line-height: clamp(1.2em, 3vw, 2em);
    margin-bottom: 1rem; }

.text-block__description {
    font-size: clamp(15px, 2vw, 45px);
    line-height: clamp(1.2em, 2vw, 2em);
    margin-bottom: 2vw; }

.services-block {
    @include greyBG(clamp(250px, 80vw, 1218px), auto);
    margin-bottom: 40px; }

.services-block__title {
    @include darkBtn(clamp(200px, 60vw, 800px), clamp(50px, 6vw, 66px));
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.6;
    padding: 2vw; }

.services-block__title > h2 {
    font-size: 2rem;
    line-height: 1rem;
    letter-spacing: 10%;
    color: #C6C6C6; }

.services-block__description {
    display: flex;
    justify-content: space-between; }



.services-block__description-name {
    @include services;
    padding-left: 3vw;
    margin-bottom: clamp(20px, 3vh, 100px); }


.services-block__description-price {
    @include services;
    padding-right: 3vw;
    text-align: right; }

.small {
    height: 272px; }

.slider-wrapper {
    @include centered;
    position: relative;
    width: clamp(250px, 90vw, 1218px);
    overflow: hidden;
    background-color: rgba(10, 0, 0, 0.7);
    border-radius: 28px;
    margin-bottom: 26px; }

.slider-wrapper__slider-track {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
    align-items: center;
    height: 100%;
    gap: 16px;
    padding: 4vw 2vw; }

.card {
    height: 308px;
    width: 374px;
    background: #141111;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    user-select: none;
    gap: 8px;
    color: white; }

.card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 24px; }

.slider-wrapper_arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: white;
    font-size: 32px;
    padding: 10px;
    cursor: pointer;
    z-index: 1; }

.arrow-left {
    left: 10px; }

.arrow-right {
    right: 10px; }

.form-entry {
    @include greyBG(1216px, auto);
    background-color: rgba(10, 0, 0, 0.7);
    padding: 3vw 0;
    margin-bottom: 56px; }

.form-entry__entry-btn {
    @include darkBtn(clamp(200px, 67vw, 465px), clamp(60px, 5vw, 72px));
    color: #FFFFFF;
    font-size: clamp(20px, 2vw, 30px);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; }

.form-entry__entry-input > form {
    display: flex;
    flex-direction: column; }

form > input {
    @include centered;
    width: clamp(200px, 45vw, 790px);
    height: auto;
    padding: 1vw;
    margin-bottom: 1vw;
    border: none;
    background-color: #D9D9D9;
    padding-left: 22px; }

.form-entry__input {
    color: #B3B3B3;
    font-size: clamp(16px, 2vw, 24px);
    border-radius: 4px; }

form > input:last-of-type {
    margin-bottom: 3vw; }

.form-entry__call-btn {
    @include centered;
    background-color: #759A74;
    border: none;
    width: auto;
    padding: 1vw;
    height: clamp(45px, 5vw, 72px);
    border-radius: 21px;
    color: #fff;
    font-size: clamp(20px, 2vw, 30px);
    letter-spacing: 10%;
    line-height: 30px; }

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: clamp(260px, 80%, 1018px);
    padding: 0 16px;
    box-sizing: border-box; }

.contacts-block {
  width: 100%;
  max-width: 1000px; }


.contacts-block__title {
    @include darkBtn(clamp(250px, 80vw, 790px), clamp(50px, 6vw, 78px));
    border-radius: 0;
    background-color: rgba(10, 0, 0, 0.38);
    color: #F5F5F5;
    display: flex;
    justify-content: center;
    align-items: center; }


.contacts-block__title > h2 {
    font-size: clamp(24px, 5vw, 48px);
    letter-spacing: 20%;
    text-align: center; }

.contacts-block__content-name {
    display: flex;
    flex-direction: column;
    text-align: left;
    font-size: clamp(14px, 2vw, 25px); }

.contacts-block__content-link {
    display: flex;
    flex-direction: column;
    text-align: right;
    font-size: clamp(14px, 2vw, 25px); }

.contacts-block__content-link {
    a {
        &:link,
        &:visited,
        &:hover,
        &:active,
        &:focus {
            color: #000;
            text-decoration: none; } } }

.contacts-block__content {
    @include greyBG(100%, auto);
    max-width: 1000px;
    line-height: 1.4;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    background: none; }

.address {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding-left: 16px;
    margin-top: 16px; }

.address > p {
    margin-left: 0;
    text-align: center;
    width: fit-content;
    font-size: clamp(14px, 4vw, 20px);
    margin-bottom: 5vw; }


input:focus {
    outline: 4px solid #000;
    color: #000; }

iframe {
    width: 90vw;
    max-width: 1000px;
    aspect-ratio: 4 / 3;
    height: auto;
    border: none;
    border-radius: 12px; }


.map-wrapper {
    margin-bottom: 3vw; }


@media (max-width: 1199px) {


    .for-media-text-block {
        font-size: 3rem;
        line-height: 3rem; }


    .form-entry__call-btn {
        margin-bottom: 28px; }

    .form-entry {
        width: 90%;
        height: auto; } }



@media (max-width: 991px) {
    .main-section__content {
        width: 95%; }


    .for-media-description {
        font-size: clamp(14px, 2.3vw, 18px);
        line-height: clamp(18px, 3vw, 24px); }

    .small {
        height: 225px; } }




@media (max-width: 767px) {

    .main-button-section {
        margin-bottom: 23px; }

    .for-media-text-block {
        font-size: 29px;
        line-height: 40px; }

    .for-media-description {
        font-size: clamp(12px, 3vw, 14px);
        line-height: clamp(16px, 4vw, 20px); }

    .for-meia-contacts {
        max-width: 528px; } }





@media (max-width: 575px) {


    .for-media-text-block {
        font-size: 10px;
        line-height: 16px; }

    .for-media-description {
        font-size: 10px;
        line-height: 12px; }

    .services-block__title {
        h2 {
            font-size: 16px; } } }


